.container-box {
  border: 1px solid;
  border-radius: 5px;
  margin: 20px auto;
  padding: 20px;
  max-width: 700px;
}

.form-filed {
  margin-bottom: 20px;
  padding: 5px;
}

.slot {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.vacname {
  color: #fc7b03;
}

.vacavl {
  color: #0077c7;
}

.vacage {
  color: #00a66c;
}

.vacfee {
  color: #6f00a6;
}

.rowdisplay {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.filter-btn {
  margin: 5px;
}

a {
  text-decoration: none;
}

.banner {
  display: block;
  margin: auto;
  padding: 10px;
  width: 100%;
  text-align: center;
  background-color: darkred;
  color: #fff;
}
